import React, { FC, useRef, useState } from 'react';
import { SocialIconsContainer, StyledSocialShare } from './styled';
import { Text } from '../Text';
import { AnimatePresence } from 'framer-motion';
import useOnClickOutside from '../../../hooks/useClickOutside';
import { LinkedinShareButton, TwitterShareButton } from 'react-share';
import { XIcon } from 'react-share';
import Linkedin from '../Icons/Socials/Linkedin';
import { colors } from '../../../utils/colors';

type Props = {
  url: string;
  title: string;
}

const SocialShare:FC<Props> = ({ url, title }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const socialShareRef = useRef(null);

  useOnClickOutside(socialShareRef, () => {
    setIsExpanded(false);
  });

  return (
    <StyledSocialShare
      isExpanded={isExpanded}
      onClick={() => setIsExpanded((st) => !st)}
      ref={socialShareRef}
    >
      <Text.Title.Bold.sm>Share</Text.Title.Bold.sm>
      <AnimatePresence mode="sync">
        {isExpanded && (
          <SocialIconsContainer
            initial={{ width: 0, opacity: 0 }}
            animate={{ width: 85, opacity: 1 }}
            exit={{ width: 0, opacity: 0 }}
          >
            <LinkedinShareButton
              url={url}
              title={title}
            >
              <Linkedin color={colors.primary.white} />
            </LinkedinShareButton>
            <TwitterShareButton url={url} title={title}>
              <XIcon size={45} />
            </TwitterShareButton>
          </SocialIconsContainer>
        )}
      </AnimatePresence>
    </StyledSocialShare>
  );
};

export default SocialShare;
